import React from "react";
import { useState } from "react";
import "./admin.css";
import axios from "axios";

export default function Admin() {
  const [name, setName] = useState("");
  const [imgCollection, setimgCollection] = useState("");
  const [producttype, setproducttype] = useState("");
  const [price, setPrice] = useState("");
  const [discount, setDiscount] = useState("");
  const [offer, setOffer] = useState("");

  const handleSubmit = async (e) => {
    e.preventDefault();
    var formData = new FormData();
    for (const key of Object.keys(imgCollection)) {
      formData.append("imgCollection", imgCollection[key]);
    }
    formData.append("name", name);
    formData.append("producttype", producttype);
    formData.append("price", "$" + price);
    formData.append("discount", "$" + discount);
    formData.append("offer", offer + "%");
    axios
      .post("http://localhost:5000/api/products/upload-images", formData, {})
      .then((res) => {
        console.log(res.data);
      });
    window.location.replace("/");
  };

  return (
    <div className="admin">
      <form className="productForm" onSubmit={handleSubmit}>
        <div className="pfTop">
          <label>Select Image</label>
          <input
            type="file"
            name="imageCollection"
            onChange={(e) => setimgCollection(e.target.files)}
            multiple
          />
          <div className="imgCont">
            {imgCollection && (
              <img src={URL.createObjectURL(imgCollection[0])} alt="" />
            )}
          </div>
          <div className="imgCont">
            {imgCollection && (
              <img src={URL.createObjectURL(imgCollection[1])} alt="" />
            )}
          </div>
          <div className="imgCont">
            {imgCollection && (
              <img src={URL.createObjectURL(imgCollection[2])} alt="" />
            )}
          </div>
        </div>
        <div className="pfBottom">
          <label>Product Name</label>
          <textarea
            type="text"
            placeholder="Product name"
            className="pName"
            rows="10"
            cols="40"
            onChange={(e) => setName(e.target.value)}
          ></textarea>

          <select
            name="selectCat"
            onChange={(e) => setproducttype(e.target.value)}
          >
            <option value="0">User type</option>
            <option value="Headphone">Headphone</option>
            <option value="Earbuds">Earbuds</option>
          </select>

          <label>Price</label>
          <input
            type="text"
            placeholder="Enter Price"
            onChange={(e) => setPrice(e.target.value)}
          />

          <label>Discount</label>
          <input
            type="text"
            placeholder="Enter Discount"
            onChange={(e) => setDiscount(e.target.value)}
          />

          <label>Offer</label>
          <input
            type="text"
            placeholder="Enter Offer"
            onChange={(e) => setOffer(e.target.value)}
          />

          <button type="submit">Create Product</button>
        </div>
      </form>
    </div>
  );
}
