import React, { useContext } from "react";
import "./navbar.css";
import { Link } from "react-router-dom";
import { Context } from "../../context/Context";

export default function Navbar() {
  const { user, dispatch } = useContext(Context);

  const handleLogout = () => {
    dispatch({ type: "LOGOUT" });
  };
  return (
    <div className="navbar">
      <div className="left">
        <span className="logo">ShopME</span>
      </div>
      <div className="mid">
        <ul className="navList">
          <Link to="/" className="itemLink">
            <li className="item active">Home</li>
          </Link>
          <Link to="/product" className="itemLink">
            <li className="item">Products</li>
          </Link>
          <Link to="/cart" className="itemLink">
            <li className="item">Cart</li>
          </Link>
          {user && user.username === "AdminHere" ? (
            <Link to="/admin" className="itemLink">
              <li className="item">Admin</li>
            </Link>
          ) : (
            <Link to="/profile" className="itemLink">
              <li className="item">Profile</li>
            </Link>
          )}

          {/* {user.username === "AdminHere" ? (
            <Link to="/profile" className="itemLink">
              <li className="item">Admin</li>
            </Link>
          ) : (
            <Link to="/profile" className="itemLink">
              <li className="item">Profile</li>
            </Link>
          )} */}
        </ul>
      </div>
      <div className="right">
        {user ? (
          <button className="navBtn" onClick={handleLogout}>
            logOut
          </button>
        ) : (
          <button className="navBtn">
            <Link to="/login" classname="itemLink">
              Sign-in
            </Link>
          </button>
        )}
      </div>
    </div>
  );
}
