import React from "react";
import { Link } from "react-router-dom";
import "./product.css";

export default function Product({ product }) {
  const PF = "http://localhost:5000/public/";

  return (
    <Link to={`/product/${product._id}`} className="prodLink">
      <div className="product">
        <div className="productTop">
          <img src={product.imgCollection[0]} alt="" className="productImg" />
          <i className="heart_icon fa-regular fa-heart"></i>
        </div>
        <div className="productMid"></div>
        <div className="productBottom">
          <span className="productDesc">{product.name}</span>
          <div className="productRating">
            <span className="totStar">3.5</span>
            <div className="stars">
              <i className="starIcon fa-solid fa-star"></i>
              <i className="starIcon fa-solid fa-star"></i>
              <i className="starIcon fa-solid fa-star"></i>
              <i className="starIcon fa-solid fa-star"></i>
              <i className="starIcon fa-solid fa-star"></i>
            </div>
          </div>
          <div className="productPriceAndOff">
            <span className="price">{product.price}</span>
            <span className="discount">{product.discount}</span>
            <span className="offer">{product.offer}</span>
          </div>
        </div>
      </div>
    </Link>
  );
}
