import React, { useContext, useEffect } from "react";
import "./singleProduct.css";
import { useLocation } from "react-router-dom";
import axios from "axios";
import { useState } from "react";
import { Context } from "../../context/Context";

export default function SingleProduct() {
  const location = useLocation();
  const path = location.pathname.split("/")[2];
  // // const {}

  const [products, setProducts] = useState([]);
  const [imgCollection, setimgCollection] = useState("");

  useEffect(() => {
    const fetchProducts = async () => {
      const res = await axios.get("/products/" + path);
      setProducts(res.data);
      setimgCollection(res.data.imgCollection);
      console.log(res);
    };
    fetchProducts();
  }, [path]);

  const { user } = useContext(Context);

  const handleAdd = async (e) => {
    e.preventDefault();
    var formData = new FormData();
    for (const key of Object.keys(imgCollection)) {
      formData.append("imgCollection", imgCollection[key]);
    }
    // formData.append("imgCollection", imgCollection);
    formData.append("_id", products._id);
    formData.append("name", products.name);
    formData.append("producttype", products.producttype);
    formData.append("price", products.price);
    formData.append("discount", products.discount);
    formData.append("offer", products.offer);
    formData.append("quantity", 1);
    formData.append("userName", user.username);
    axios
      .post("http://localhost:5000/api/carts/upload-images", formData, {})
      .then((res) => {
        console.log(res.data);
      });
    window.location.replace("/cart");
  };

  return (
    <div className="singleProducts">
      <div className="spLeft">
        <div className="splLeft">
          <img src={imgCollection[0]} alt="" className="mainProdImg" />
        </div>
        <div className="splRight">
          <div className="imgCont">
            <img src={imgCollection[1]} alt="" className="secondaryImg-1" />
          </div>
          <div className="imgCont">
            <img src={imgCollection[2]} alt="" className="secondaryImg-2" />
          </div>
        </div>
      </div>
      <div className="spRight">
        <span className="prodName">{products.name}</span>
        <span className="prodDesc">{products.name}</span>
        <div className="prodRating">
          <span className="totStar">3.5</span>
          <div className="stars">
            <i className="starIcon fa-solid fa-star"></i>
            <i className="starIcon fa-solid fa-star"></i>
            <i className="starIcon fa-solid fa-star"></i>
            <i className="starIcon fa-solid fa-star"></i>
            <i className="starIcon fa-solid fa-star"></i>
          </div>
        </div>
        <div className="prodPrice">
          <span className="price">{products.price}</span>
          <span className="discount">{products.discount}</span>
          <span className="offer">{products.offer}</span>
        </div>
        <div className="btns">
          <button className="bnBtn">Buy Now</button>
          <button className="atcBtn" onClick={handleAdd}>
            Add to Cart
          </button>
        </div>
        <div className="prodReviews">
          <span className="review">Reviews</span>
          <input type="text" placeholder="Write your reviews" />
          <button className="postBtn">Submit</button>
        </div>
      </div>
    </div>
  );
}
